@import url(https://fonts.googleapis.com/css?family=Heebo:100,300,400);
body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
body {
    font-family: 'Heebo', sans-serif;
    font-size: 18px;
    font-weight: 400;
}

h1 {
    color: #333;
}

h1, h2, h3 {
    font-weight: 300;
}

.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.navbar {
    position: fixed;
    width: 100%;
    height: 60px;
    padding: 0;
    margin: 0;
    top: 0;
    background-color: #252525;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    z-index: 1000;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.navbar-logo {
    width: 50px;
    padding: 10px;
}

.navbar-logo img {
    height: 100%;
    width: 100%;
}

.navlinks {
    padding: 0;
    margin: 0;
    height: 100%;
    list-style: none;
    display: flex;
    flex-direction: row;
}

.navlink {
    text-decoration: none;
    font-weight: 300;
}

.navlink div {
    width: 100px;
    text-align: center;
    vertical-align: middle;
    line-height: 60px;
    color: white;
}

.navlink div:hover {
    background-color: #555;
}

.nav-toggle {
    text-align: center;
    vertical-align: middle;
    line-height: 60px;
    color: white;
    font-size: 2rem;
    display: none;
}

.nav-toggle span:hover {
    cursor: pointer;
}

@media (max-width: 730px) {
    .navbar-logo {
        display: none;
    }
    nav {
        width: 100%;
    }
    .navlinks {
        flex-direction: column;
        overflow: hidden;
    }
    .navlink div {
        width: 100%;
    }
    .show-nav-toggle {
        display: block;
    }
    .hide-nav-toggle {
        display: none;
    }
    .navbar-open {
        height: 100vh;
    }
}
.content-wrapper {
    display: flex;
    justify-content: center;
}

.content-section {
    padding: 4rem;
    padding-bottom: 0;
}

.content-section h1 {
    text-align: center;
}

@media (max-width: 730px) {
    .content-section {
        padding-left: 1rem;
        padding-right: 1rem;
    }
}

.content-width-sm {
    max-width: 800px;
}

.content-width-md {
    width: 100%;
}

.content-width-full {
    width: 100%;
}

@media (min-width: 1200px) {
    .content-width-md {
        width: 1200px;
    }
}
.header {
    height: 100vh;
    background-color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header-main-group {
    width: 100%;
    margin: auto 0 auto 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header-logo {
    width: 100%;
    height: 200px;
    opacity: 0;
    -webkit-animation: logo-appear 0.5s ease-in 1.2s forwards;
            animation: logo-appear 0.5s ease-in 1.2s forwards;
}

.header-line {
    width: 660px;
    height: 1px;
    text-align: center;
}

.header-line svg {
    width: 0%;
    height: 100%;
    stroke: white;
    stroke-width: 100%;
    -webkit-animation: line-appear 1s ease forwards 0.5s;
            animation: line-appear 1s ease forwards 0.5s;
}

.header-title {
    padding: 1rem;
    padding-left: 1.5rem;
    color: white;
    text-align: center;
    font-size: 3rem;
    font-weight: 100;
    letter-spacing: 10px;
    opacity: 0;
    -webkit-animation: title-appear 0.5s ease-in 1.2s forwards;
            animation: title-appear 0.5s ease-in 1.2s forwards;
}

.header-social {
    opacity: 0;
    -webkit-animation: social-appear 2s ease 1.5s forwards;
            animation: social-appear 2s ease 1.5s forwards;
}

.header-social-button {
    color: #666;
    font-size: 4rem;
    margin: 1rem;
}

.header-go-down {
    color: #888;
    font-size: 4rem;
    opacity: 0;
    -webkit-animation:
        go-down-appear 2s 2s forwards,
        go-down-bounce infinite 4s ease 2s;
            animation:
        go-down-appear 2s 2s forwards,
        go-down-bounce infinite 4s ease 2s;
}

.header-go-down:hover {
    cursor: pointer;
}

@media (max-width: 730px) {
    .header-title {
        font-size: 2rem;
    }
    .header-logo {
        height: 100px;
    }
    .header-social-button {
        font-size: 2rem;
    }
    .header-line {
        width: 500px;
    }
}

@media (max-width: 554px) {
    .header-line {
        width: 250px;
    }
}

@media (max-height: 550px) {
    .header-logo {
        padding-top: 5rem;
    }
}

@-webkit-keyframes logo-appear {
    from {
        opacity: 0;
        -webkit-filter: blur(10px);
                filter: blur(10px);
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    to {
        opacity: 1;
        -webkit-filter: blur(0);
                filter: blur(0);
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes logo-appear {
    from {
        opacity: 0;
        -webkit-filter: blur(10px);
                filter: blur(10px);
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
    }
    to {
        opacity: 1;
        -webkit-filter: blur(0);
                filter: blur(0);
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes line-appear {
    from {
        width: 0;
    }
    to {
        width: 100%;
    }
}

@keyframes line-appear {
    from {
        width: 0;
    }
    to {
        width: 100%;
    }
}

@-webkit-keyframes title-appear {
    from {
        opacity: 0;
        -webkit-filter: blur(10px);
                filter: blur(10px);
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    to {
        opacity: 1;
        -webkit-filter: blur(0);
                filter: blur(0);
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes title-appear {
    from {
        opacity: 0;
        -webkit-filter: blur(10px);
                filter: blur(10px);
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    to {
        opacity: 1;
        -webkit-filter: blur(0);
                filter: blur(0);
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes social-appear {
    from, 20% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
        opacity: 0;
    }
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@keyframes social-appear {
    from, 20% {
        -webkit-transform: translateY(20px);
                transform: translateY(20px);
        opacity: 0;
    }
    to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
    }
}

@-webkit-keyframes go-down-appear {
    from {
        opacity: 0;
        -webkit-filter: blur(12px);
                filter: blur(12px);
    }
    to {
        opacity: 1;
        -webkit-filter: blur(0);
                filter: blur(0);
    }
}

@keyframes go-down-appear {
    from {
        opacity: 0;
        -webkit-filter: blur(12px);
                filter: blur(12px);
    }
    to {
        opacity: 1;
        -webkit-filter: blur(0);
                filter: blur(0);
    }
}

@-webkit-keyframes go-down-bounce { 
    0%, 45%, 55%, 65% {-webkit-transform: translateY(0);transform: translateY(0);}
    50%, 60% {-webkit-transform: translateY(10px);transform: translateY(10px);}
}

@keyframes go-down-bounce { 
    0%, 45%, 55%, 65% {-webkit-transform: translateY(0);transform: translateY(0);}
    50%, 60% {-webkit-transform: translateY(10px);transform: translateY(10px);}
}
.projects-content {
    max-width: 800px;
}

@media (min-width: 800px) {
    .projects-content h1 {
        text-align: center;
    }
}

.project-cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 20px;
    justify-items: center;
}

@media (max-width: 800px) {
    .project-cards {
        grid-template-columns: 1fr;
    }
}
.project-card {
    box-shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
    width: 100%;
    color: white;
    text-decoration: none;
    background-color: #444;
    background-image: -webkit-gradient(linear, left top, right bottom, from(#333), to(#777));
    background-image: -webkit-linear-gradient(top left, #333, #777);
    background-image: linear-gradient(to bottom right, #333, #777);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 110%;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.project-card:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.project-card-content {
    position: relative;
    margin: 1rem;
    margin-left: 2rem;
    height: 120px;
}

.project-card-content h2 {
    margin: 0;
    margin-bottom: 0.5rem;
}

.project-card-status {
    position: absolute;
    bottom: 0;
    right: 0;
    color: #BBB;
    font-size: 1rem;
}

.project-classified {
    color: rgb(130, 55, 55);
}

.project-card-small {
}

.project-card-medium {
    grid-column: 1 / -1;
}

.project-card-large {
    grid-column: 1 / -1;
}

@media (min-width: 800px) {
    .project-card-large .project-card-content {
        height: 200px;
    }
}
.member-card {
    box-shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
    max-width: 300px;
}

.member-card img {
    width: 100%;
}

.member-card-content {
    display: flex;
    justify-content: space-between;
    margin: 10px;
}

.member-card-info {
    text-align: left;
}

.member-card-info > * {
    margin: 0;
}

.member-card-content p {
    color: #444;
    font-weight: 300;
}

.member-card-social {
    text-align: center;
}

.member-card-icon {
    font-size: 2rem;
    color: #555;
    padding: 1rem;
}
#our-team {
    text-align: center;
    padding: 2rem;
    padding-top: 4rem;
}

.member-cards {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: 1fr;
    justify-items: center;
}

@media (min-width: 600px) {
    .member-cards {
        grid-template-columns: 1fr 1fr;
    }
}

@media (min-width: 1000px) {
    .member-cards {
        grid-template-columns: 1fr 1fr 1fr;
    }
}

@media (min-width: 1200px) {
    .member-cards {
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
}
.partners-content {
    max-width: 800px;
    padding-bottom: 2rem;
}

.partner-cards {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 20px;
    justify-items: center;
}

@media (max-width: 800px) {
    .partner-cards {
        grid-template-columns: 1fr;
    }
}

.partner-image {
    width: 300px;
}
.contact-content {
    width: 600px;
    margin-bottom: 2rem;
}

.sent-alert {
    color: green;
}

@media (max-width: 730px) {
    .contact-content {
        width: 100%;
    }
}
.contact-form {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.input-field {
    height: 30px;
    margin-bottom: 1rem;
}

.message-field {
    resize: none;
    margin-bottom: 1rem;
}

.button-group {
    display: flex;
    justify-content: flex-end;
}

.button {
    width: 100px;
    height: 30px;
    background-color: grey;
    /* background-color: #252525; */
    color: white;
    border: none;
    cursor: not-allowed;
}

@media (max-width: 400px) {
    .button {
        width: 100%;
    }
}
.privacy-content {
    padding: 4rem;
}

.privacy-content p {
    margin-top: 0;
}

.privacy-content h2 {
    margin-bottom: 0;
}

@media (min-width: 1200px) {
    .privacy-content {
        margin-top: 100px;
        margin-bottom: 100px;
        max-width: 1000px;
        box-shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
    }
    .privacy-content h1,h2,h3 {
        margin-top: 0;
    }
}

@media (max-width: 730px) {
    .privacy-content {
        padding-right: 1rem;
        padding-left: 1rem;
    }
}
.notfound-content {
    padding: 4rem;
}
footer {
    max-width: 100%;
    color: white;
    font-weight: 100;
    background-color: #252525;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer {
    max-width: 800px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
}

footer div {
    text-align: center;
    vertical-align: middle;
    line-height: 50px;
    min-width: 200px;
}

.footer-link {
    text-decoration: none;
    color: white;
}

.footer-link:hover {
    cursor: pointer;
}

@media (max-width: 550px) {
    .footer {
        grid-template-columns: 1fr;
    }
}
